<template>
  <div class="mvTop">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="mvList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <van-list
      v-if="mvList != ''"
      :immediate-check="false"
      v-model="loading"
      :finished="finished"
      finished-text="查看更多请前往网易云APP~"
      @load="onLoad"
    >
      <div class="top-item" v-for="(item, index) in mvList" :key="item.id">
        <div @click="playMv(index)" class="img">
          <img :src="item.cover" alt="" />
          <span></span>
          <span class="iconfont icon-caret-right">{{ item.playCount }}</span>
        </div>
        <div class="name">
          <span
            :class="[
              'index',
              index == 0 || index == 1 || index == 2 ? 'top-color' : '',
            ]"
            >{{ index + 1 }}</span
          >
          <p class="mv-name">{{ item.name }}</p>
        </div>
        <div class="auther">
          <span class="line">-</span>
          <p class="mv-auther">{{ item.artistName }}</p>
        </div>
      </div>
    </van-list>
    <van-popup
      :style="{ height: '100%', width: '100%' }"
      v-model="$store.state.showPlayMv"
    >
      <playMv v-if="$store.state.showPlayMv" ref="getData"/>
    </van-popup>
  </div>
</template>

<script>
import { getMVTop } from "../../NetRequest/MV";
import { toStringNum } from "../../common/common.js";
import playMv from "./playMv";
export default {
  components: { playMv },
  data() {
    return {
      mvList: [],
      offset: 0,
      loading: false,
      finished: false,
      onLoadCount: 0,
      hasMore: true,
      resultLength: 1,
      topOffset:'0'
    };
  },
  mounted() {
    this.getMVTopData();
    this.$bus.$on("closeModle", this.closeModle);
  },
  methods: {
    async getMVTopData() {
      if (!this.hasMore || this.resultLength == 0) {
        this.finished = true;
        return;
      }
      // console.log(this.area);
      const res = await getMVTop(this.area, 5, this.offset);
      // console.log(res);
      this.resultLength = res.data.data.length;
      this.hasMore = res.data.hasMore;
      for (const item of res.data.data) {
        this.mvList.push({
          id: item.id,
          name: item.name, // mv名称
          cover: item.cover, // mv封面
          artistName: item.artistName, // 创作者
          playCount: toStringNum(item.playCount), // 播放量
        });
      }
      this.offset += 5;
      this.loading = false;
    },
    onLoad() {
      if (++this.onLoadCount >= 9) {
        this.finished = true;
        return;
      }
      setTimeout(() => {
        this.getMVTopData();
      }, 2000);
    },
    playMv(index) {
      // console.log(mvId);
      this.$store.state.isPlaying = true
      this.$bus.$emit('handlePlay')
      this.$store.state.palyMvInfo = this.mvList[index];
      this.$store.state.showPlayMv = true;
      this.stopBodyScroll(true);
      setTimeout(()=>{
        // console.log('setTimeout')
        this.$refs.getData.getData()
      },1500)
      document.body.addEventListener("touchmove", this.bodyScroll, {
        passive: false,
      });
      document.querySelector("body").style.position = "fixed";
      document.querySelector("body").style.width = "100%";
      this.$store.state.showControlBar = false
    },
    bodyScroll(event) {
      event.preventDefault();
    },
    stopBodyScroll(isFixed) {
      let body_scroll = document.querySelector("body");
      if (isFixed) {
        this.topOffset = window.scrollY;
        body_scroll.style.position = "fixed";
        body_scroll.style.top = -this.topOffset + "px";
      } else {
        body_scroll.style.position = "";
        window.scrollTo(0, this.topOffset);
      }
    },
    closeModle() {
      // console.log('closeModle');
      this.stopBodyScroll(false);
      document.body.removeEventListener("touchmove", this.bodyScroll, {
        passive: false,
      });
      document.querySelector("body").style.position = "initial";
      document.querySelector("body").style.height = "auto";
    },
  },
  beforeDestroy() {
    this.$bus.$off('closeModlecloseModle')
  },
  props: {
    area: {
      type: String,
    },
  },
};
</script>

<style scoped>
.loading {
  margin-top: 20px;
  margin-left: 40%;
}
.mvTop {
  margin-bottom: 100px;
}
.mvTop-fixed {
  position: fixed;
}
.top-item {
  padding: 15px;
}
.img {
  position: relative;
}
.img img {
  width: 100%;
  height: 200px;
  border-radius: 15px;
}
.icon-caret-right {
  position: absolute;
  top: 5px;
  right: 5px;
  color: #eee;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 3px 5px;
  border-radius: 10px;
}
.name,
.auther {
  display: flex;
  justify-content: left;
}
p {
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 200px;
  font-size: 13px;
}
.index,
.line {
  width: 20px;
  margin-right: 30px;
  color: #999;
}
.index {
  font-size: 25px;
}
.mv-name {
  font-weight: 700;
}
.name {
  line-height: 30px;
  text-align: center;
  margin-top: 5px;
}
.auther {
  color: #999;
  line-height: 20px;
  text-align: center;
}
.top-color {
  color: red;
}
.show-play {
  z-index: 999;
}
</style>